.songs__container {
  width: 100%;
  height: fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 60px 5px;
  -background-color: hotpink;
}
@media (min-width: 1200px) {
  .songs__container {
    margin-left: 10%;
  }
}

.songs__container > h2 {
  margin-bottom: 5px;
  font-size: 26px;
}

.songs__covers {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  -background-color: blue;
  padding: 0 5%;
}