.song__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  -border: 2px solid black;
  width: 90%;
  height: fit-content;
}

.song__container > img {
  width: 100%;
  border-radius: 15px;
}

.song__container > h3 {
  margin-top: 3px;
}

.song__container > h4 {
  font-size: 14px;
}